import { Dialog, DialogContent } from '@material-ui/core';
import _get from 'lodash.get';
import config from '../../../../config';
import { TOffer } from '../../../../types';
import FormModalContent from './FormModalContent';

type Props = {
	open: boolean;
	offer: TOffer | undefined;
	handleClickCloseDialog: () => void;
};

const CounterOfferDialog = (props: Props) => {
	const { open, offer, handleClickCloseDialog } = props;

	const stepId = _get(offer, ['StepId']);
	const formCounter = _get(offer, ['CounterOffer', 'Forms']).find(
		(form: any) =>
			_get(form, ['FormTypeId']) === config.formTypes.counterOfferSubmission
	);
	const formId = _get(formCounter, ['FormId']);

	const counterOfferId = _get(offer, ['CounterOffer', 'CounterOfferId']);

	return (
		<Dialog
			open={open}
			aria-labelledby='customized-dialog-title'
			scroll='paper'
			fullWidth
			maxWidth='md'
			onClose={handleClickCloseDialog}
			id='counterForm'
		>
			<DialogContent>
				<div
					className={`custom-dialog-content ${
						stepId === config.offerSteps.advisorNeedToReviewNewOffer
							? 'content-without-submit'
							: ''
					}`}
				>
					<FormModalContent
						offerId={counterOfferId}
						formId={formId}
						handleClickCloseDialog={handleClickCloseDialog}
					/>
				</div>
			</DialogContent>
		</Dialog>
	);
};

export default CounterOfferDialog;
